import React, { Component } from 'react'
import MainNav from "../Navigation/MainNav";
import { InputValidate } from "../../redux/actions/Inputs";
export default class screen1 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            password: null,
            errors: {
                password: null
            },
            passIcon: "fa-eye"
        };
    }
    passwordHandler = event => {
        const classContain = event.target.className.split(" ")[1];
        if (classContain === "fa-eye") {
            this.setState({
                passIcon: "fa-eye-slash"
            })
        }
        else {
            this.setState({
                passIcon: "fa-eye"
            })
        }
    }
    HandleChanging = e => {
        let errors = InputValidate(e, this.state);
        this.setState(errors);
    }
    render() {
        return (
            <React.Fragment>
                <div className="container-fluid" id="Forget-1">
                    <img src="./img/logo.png" alt="" />
                    <div className="row">
                        <div className="col-12">
                            <span>Password Reset</span>
                            <hr />
                            <p>Your password will be reset so you can get back to watching.</p>
                            <div className="inputs">
                                <label htmlFor="password">New Password</label>
                                <div className="input-group">
                                    <input type={this.state.passIcon === "fa-eye" ? "password" : "text"} name="password" id="password" placeholder="Enter Your Password Here" onChange={this.HandleChanging} />
                                    <i className={`fas ${this.state.passIcon}`} onClick={this.passwordHandler}></i>
                                </div>
                            </div>
                            <div className="inputs">
                                <label htmlFor="password">Confirm Password</label>
                                <div className="input-group">
                                    <input type={this.state.passIcon === "fa-eye" ? "password" : "text"} name="password" id="password" placeholder="Enter Your Password Here" onChange={this.HandleChanging} />
                                    <i className={`fas ${this.state.passIcon}`} onClick={this.passwordHandler}></i>
                                </div>
                            </div>
                            <button>Reset Password</button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
